import { Component, Element, Event, EventEmitter, Host, Prop, h } from '@stencil/core';
import Quill, { Delta } from 'quill';

@Component({
  tag: 'storm-text-editor',
  styleUrl: 'storm-text-editor.css',
  shadow: false,
})
export class StormTextEditor {
  @Element() host: HTMLElement;
  @Prop({ mutable: true }) value: string;
  @Prop() label: string;
  @Event() change: EventEmitter<string>;
  quillRef: HTMLElement;
  quill: Quill;
  qlEditor: HTMLDivElement;
  labelDivRef: HTMLDivElement;

  componentDidLoad() {
    this.quill = new Quill(this.quillRef, {
      theme: 'snow',
      modules: {
        toolbar: [
          [{ header: [6, 5, 4, 3, 2, 1] }],
          ['italic', 'underline', 'strike'],
          [{ color: [] }, { background: [] }],
          [{ list: 'ordered' }, { list: 'bullet' }],
          [{ align: [] }],
        ],
      },
    });
    this.quill.format('header', 6);
    this.qlEditor = this.host.querySelector('.ql-container').querySelector('.ql-editor');

    if (this.value) this.qlEditor.innerHTML = this.value;

    if (this.label) this.handleFocus(this.qlEditor);

    this.quill.on('text-change', (_delta: Delta, _oldContents: Delta, _source: string) => {
      this.value = this.qlEditor.innerHTML;
      this.change.emit(this.qlEditor.innerHTML);
    });
  }

  labelRender() {
    if (this.label) {
      return (
        <div class="input-group input-group-static" ref={el => (this.labelDivRef = el)}>
          <label>{this.label}</label>
        </div>
      );
    }
  }

  handleFocus(el: HTMLElement) {
    el.addEventListener('focusin', () => this.labelDivRef.classList.add('is-focused'));
    el.addEventListener('focusout', () => this.labelDivRef.classList.remove('is-focused'));
  }

  render() {
    return (
      <Host>
        {this.labelRender()}
        <div ref={el => (this.quillRef = el)} />
      </Host>
    );
  }
}
